/* ================================
/* Guide
/* ================================
float, flex
(type of element)
color
font
dimensions
padding
margin
border
/* ================================ */

/* ================================
/* Shims
/* ================================ */

*:focus {
  outline: none;
}

* {
  font-family: 'Trebuchet MS', Tahoma, sans-serif;
  font-size: 14px;
  padding: 0;
  margin: 0;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

textarea {
  background-color: inherit;
}

/* ================================
/* Colors
/* ================================ */

@appColor: rgb(219, 219, 219);
@appColor2: rgb(58, 180, 139);

@textColor: @buttonColor;
// @textDirtyColor: #c99;
// @listColor: #f90;

@buttonColor: rgb(62, 192, 186);
@codeButtonColor: #d95;
@systemButtonColor: #00a;
@searchButtonColor: #fd0;
@testButtonColor: #0aa;

@hoverColor: @appColor2;
@selectedColor: @buttonColor;

@cleanColor: #3d3;
@dirtyColor: #d33;

/* ================================
/* Mixin
/* ================================ */

#mixin {
  .bordered {
    border: 2px solid;
    border-radius: 10px;
    padding: 10px;
  }

  .pane {
    .bordered;
    margin-right: 10px;
    background-color: @appColor;
    height: 600px;
  }

  .button {
    background-color: @buttonColor;
    margin-right: 4px;
    margin-bottom: 4px;
    padding: 5px;
    min-width: 60px;
    border-radius: 5px;
  }
}

/* ================================
/* General
/* ================================ */

body {
  background-color: @appColor2;
  padding: 10px;
}

button {
  #mixin.button
}

button.search {
  background-color: @searchButtonColor
}

button.system {
  background-color: @systemButtonColor
}

button[data-name*='UT'] {
  background-color: @testButtonColor;
}

button[data-name='GenCode'] {
  background-color: @codeButtonColor;
}

textarea {
  tab-size: 4;
  line-height: 20px;
}

ul {
  list-style-type: none;
}

input {
  background-color: @textColor;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 10px;
}

/* ================================
/* Dropdown
/* ================================ */
.dd-wrapper {
  display: inline-block;
}

.dd-wrapper:hover {
  cursor: pointer;
}

.dd-list-item, .dd-header-title {
  width: 200px;
  #mixin.button
}

/* ================================
/* Orange
/* ================================ */

#divSearch input {
  height: 30px;
  width: 100%
}

#knowtLinks {
  padding-top: 5px;
  padding-bottom: 10px;
}

.KnowtLink {
  #mixin.button
}

#ul_keys li:hover, .FinderColumn li:hover {
  background-color: @hoverColor;
}

#divHeader {
  #mixin.bordered;
  display: inline-block;
  margin-bottom: 10px;
}

#divFinder {
  #mixin.bordered;
  display: flex;
  width: 100%;
  margin-bottom: 10px;
  padding-right: 0;
}

#txt_text {
  flex: 0 1 auto;
  width: 700px;
}

#txt_log {
  flex: 0 1 auto;
  width: 350px;
}

#divDisplay {
  display: none;
  flex: 0 1 auto;
  width: 800px;
}

#txaMessages {
  flex: 0 1000000 auto;
  width: 400px;
}

.TextPane, .FinderColumn, #ul_keys, #divDisplay {
  #mixin.pane;
}

.Finder {
  flex: none;
  display: flex;
  margin-right: 10px;
}

.FinderColumn {
  margin-right: 0;
}

.ListPane .selected {
  background-color: @selectedColor;
}

.Finder .selected {
  background-color: @selectedColor;
}

.log {
  width: 50px;
  word-break: break-all;
  word-wrap: break-word;
}

#btnTodo {
  background-color: orange;
}

#btnToday {
  background-color: red;
}

.isDirty {
  background-color: red;
}

.actionButton {
  background-color: red;
}

.btnTest {
  background-color: green;
}

#ul_keys {
  display: inline-block;
}

#panes {
  display: flex;
}

.getsDirty.isDirty {
  background-color: red;
}

.getsDirty {
  background-color: green;
}

button.on {
  border: 2px solid orange;
}